import * as Extension from './extension'

// 
const RightMenuWidth = chrome.i18n.getUILanguage() === 'ru' ? 210 : 190

// popupvue
let vm = null

let hoverTimer = null

// let bg = chrome.extension.getBackgroundPage();
/**
 * 
 */
function init(t) {
  // vm
  vm = t

  // 
  document.addEventListener('contextmenu', function (e) {
    e.preventDefault()
  })

  // 
  localStorage.version = chrome.runtime.getManifest().version

  // 
  document.addEventListener('click', (e) => {
    if (e.target && e.target.className == 'options') {
      e.preventDefault()
      e.stopPropagation()
      e.stopImmediatePropagation()
    } else {
      e.preventDefault()
      resetHandle()
      cancelSearch()
    }
    // e.preventDefault()
    // resetHandle()
    // cancelSearch()
  })
}

/**
 * tabid
 */
function getCurrentTabId(callback) {
  chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
    if (callback) {
      callback(tabs.length ? tabs[0].id : null)
    }
  })
}

/**
 * 
 */
function getPositionByExt(item, info) {
  const itemEle = document.querySelector(`[data-id=${item.id}]`)
  const showGap = 16

  const extSize = itemEle.offsetHeight
  const bodyWidth = document.querySelector('#' + vm.theme + 'popup').offsetWidth
  let extLeft = itemEle.offsetLeft
  let extTop = itemEle.offsetTop
  let atLeft = false
  let adviseMaxWidth = null
  let left = 0
  let right = 0
  let top = 0

  // 
  let rightSideSpace = bodyWidth - extSize - extLeft - showGap
  let leftSideSpace = extLeft - showGap
  if (rightSideSpace > item.showMaxWidth || leftSideSpace < rightSideSpace) {
    if (info.isHoverMenu) {
      left = `${extLeft + extSize + showGap}px`
    } else {
      left = `${extLeft + extSize + showGap - 15}px`
    }
    right = 'unset'
    adviseMaxWidth = rightSideSpace - 10
  } else {
    atLeft = true
    adviseMaxWidth = leftSideSpace - 10
    left = 'unset'
    if (info.isHoverMenu) {
      right = `${bodyWidth - extLeft + showGap}px`
    } else {
      right = `${bodyWidth - extLeft + showGap - 15}px`
    }
  }

  // 
  if (info.height > extSize) {
    top = `${extTop - (info.height - extSize) / 2}px`
  } else {
    top = `${extTop + (extSize - info.height) / 2}px`
  }
  // console.table({
  //   extSize, extLeft, extTop, showMaxWidth: item.showMaxWidth, rightSideSpace, leftSideSpace, atLeft, adviseMaxWidth, left, right, top
  // })

  return {
    left,
    right,
    top,
    atLeft,
    adviseMaxWidth,
  }
}

/**
 * 
 */
function getPositionByExtFlat(item, info) {
  const itemEle = document.querySelector(`[data-id=${item.id}]`)
  const itemNameEle = document.querySelector(`[name-data-id=${item.id}]`)
  const showGap = 6
  const extSize = itemEle.offsetHeight
  const extNameWidth = itemNameEle.offsetWidth
  const bodyWidth = document.querySelector('#' + vm.theme + 'popup').offsetWidth
  let extLeft = itemEle.offsetLeft
  let extTop = itemEle.offsetTop
  let atLeft = false
  let adviseMaxWidth = null
  let left = 0
  let right = 0
  let top = 0

  // 
  let rightSideSpace = bodyWidth - extSize - extNameWidth - extLeft - showGap
  let leftSideSpace = extLeft - showGap

  left = `${extLeft + extSize + showGap + extNameWidth}px`
  right = 'unset'
  adviseMaxWidth = rightSideSpace - 10

  // 
  top = `${extTop - extSize + 32}px`

  // console.table({
  //   extSize, extLeft, extTop, showMaxWidth: item.showMaxWidth, rightSideSpace, leftSideSpace, atLeft, adviseMaxWidth, left, right, top
  // })

  return {
    left,
    right,
    top,
    atLeft,
    adviseMaxWidth,
  }
}

/**
 * 
 */
function showMenu(item, isHoverMenu) {
  hideMenu()
  clearTimeout(hoverTimer)
  hoverTimer = null

  setTimeout(() => {
    // 
    let content = [
      {
        name: item.isLocked ? vm.i18n.rightLock_unlock : vm.i18n.rightLock_lock,
        handle: () => {
          hideMenu()
          if (item.isLocked) {
            Extension.unlock(item)
          } else {
            Extension.lock(item)
          }
        },
        disabled: false,
      },
      {
        name: vm.i18n.rightOption,
        handle: () => {
          hideMenu()
          if (item.optionsUrl) {
            if (!item.enabled) {
              Extension.onoff(item, () => {
                chrome.tabs.create({
                  url: item.optionsUrl,
                })
              })
            } else {
              chrome.tabs.create({
                url: item.optionsUrl,
              })
            }
          }
        },
        disabled: !item.optionsUrl,
      },
      {
        name: vm.i18n.rightUninstall,
        handle: () => {
          hideMenu()
          Extension.uninstall(item)
        },
        disabled: false,
      },
      {
        name: vm.i18n.rightHomepage,
        handle: () => {
          hideMenu()
          if (item.installType !== 'development') {
            chrome.tabs.create({
              url: item.homepageUrl,
            })
          }
        },
        disabled: item.installType === 'development',
      },
    ]
    if (item.isApp) {
      content.splice(1, 1, {
        name: vm.i18n.rightAppLaunch,
        handle: () => {
          hideMenu()
          if (!item.enabled) {
            Extension.onoff(item, () => {
              chrome.management.launchApp(item.id)
            })
          } else {
            chrome.management.launchApp(item.id)
          }
        },
        disabled: false,
      })
    }

    let position = getPositionByExt(item, {
      width: RightMenuWidth,
      height: 84,
      isHoverMenu,
    })

    vm.rightMenu = {
      name: item.shortName,
      showClass: position.atLeft ? 'showInfoLeft' : 'showInfoRight',
      left: position.left,
      right: position.right,
      top: position.top,
      hover: false,
      backgroundColor: item.showColor[item.showMark],
      content,
    }
  }, 0)
}

