/**
 * 
 */
function search() {
  let text = vm.searcher.text
    .trim()
    .replace(/\s{2,}/g, ' ')
    .toLowerCase()
  if (text) {
    setTimeout(() => {
      vm.searcher.doing = true
      let queryArr = text.split(/\s/)
      let searchRes = vm.ext.extList.filter((item) => {
        let extInfo = (item.name + item.description).toLowerCase()
        let isSearched = queryArr.some((q) => extInfo.includes(q))
        item['isSearched'] = isSearched
        return isSearched
      })
      if (searchRes.length === 0) {
        vm.searcher.empty = true
      } else {
        vm.searcher.empty = false
      }
    })
  } else {
    vm.searcher.doing = false
    vm.searcher.empty = false
    vm.ext.extList.forEach((item) => {
      if (item['isSearched']) {
        item['isSearched'] = false
      }
    })
  }
}
function cancelSearch() {
  vm.searcher = {
    doing: false,
    text: '',
    empty: false,
  }
}

// 
function onoff(item) {
  // Hover
  clearTimeout(hoverTimer)
  hoverTimer = null
  Extension.onoff(item)
  resetHandle()
  track('GA_EVENT', {
    category: 'extention',
    action: item.enabled ? 'on' : 'off',
    label: item.shortName,
  })
}
// 
function clear() {
  Extension.clear()
}

function track(type, data) {
  chrome.runtime.sendMessage({
    type: type,
    data: data,
  })
}

// 
let moreTimer = null
function showMore() {
  moreTimer = setTimeout(() => {
    vm.moreShow = true
  }, 200)
}
function hideMore() {
  clearTimeout(moreTimer)
  vm.moreShow = false
}

/**
 * 
 */
let groupTimer = null
function showGroup() {
  groupTimer = setTimeout(() => {
    vm.groupShow = true
  }, 200)
}
function hideGroup() {
  clearTimeout(groupTimer)
  vm.groupShow = false
}
function changeGroup(index) {
  vm.groupIndex = index
  vm.groupShow = false

  setTimeout(() => {
    let lockObj = vm.group.list[index].lock
    let fixObj = vm.fix
    let reuledObj = vm.ruleable

    vm.ext.extList.forEach((item) => {
      // 
      if (!item.enabled && fixObj[item.id]) {
        chrome.management.setEnabled(item.id, true)
        // bg.updateState(item.id, true)
        item.enabled = true
      }
      if (item.enabled && !lockObj[item.id] && !fixObj[item.id] && !reuledObj[item.id]) {
        chrome.management.setEnabled(item.id, false)
        // bg.updateState(item.id, false)
        item.enabled = false
      } else if (!item.enabled && lockObj[item.id]) {
        chrome.management.setEnabled(item.id, true)
        // bg.updateState(item.id, true)
        item.enabled = true
      }
    })
    resetHandle()
    chrome.action.setBadgeText({ text: '' })
  }, 50)
}
function setGroup() {
  vm.groupShow = false
  chrome.tabs.create({
    url: `${chrome.runtime.getManifest().options_page}#group`,
  })
}

function setMore() {
  vm.moreShow = false
  chrome.tabs.create({
    url: `${chrome.runtime.getManifest().options_page}`,
  })
}
// 
function detectColorTheme() {
  if (!window.matchMedia) {
    ////matchMedia method not supported
    return ''
  } else if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
    return 'dark'
  }
  return ''
}

