<template>
  <div
    :id="theme + 'popup'"
    :data-lan="language"
    :class="'icon-size-' + getShowIconSize"
    :style="'width:' + getShowWindowSize + 'px'"
  >
    <Dialog
      v-model="showDialog"
      :dialogTitle="dialogTitle"
      :dialogContent="dialogContent"
      :confirmText="confirmText"
      :dangerText="dangerText"
      :cancelText="cancelText"
      :type="dialogType"
      v-on:cancel="clickCancel()"
      @danger="clickDanger()"
    ></Dialog>
    <div
      :id="theme + 'wrap'"
      :searching="searcher.doing"
      :isShowName="isShowName"
      v-if="ext.extList.length > 0"
    >
      <div id="search">
        <div id="searchBox">
          <input
            type="text"
            class="searchInput searcher"
            :style="
              theme ? 'background-color: #313131; color: #717171' : 'background-color: #f5f5f5;'
            "
            v-model="searcher.text"
            :placeholder="i18n.searcherPlaceholder"
            @input="search"
            v-focus
            @mouseenter="focus"
            @keydown="searchByChromeStore"
          />
          <svg
            width="24px"
            height="24px"
            class="searchBtn searchEmpty"
            viewBox="0 0 24 24"
            @mousedown="cancelSearch"
          >
            <path
              d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"
            ></path>
          </svg>
          <svg
            width="24px"
            height="24px"
            class="searchBtn searchEnter"
            viewBox="-100 -100 1200 1200"
            @mousedown="enterSearch"
            @mouseenter="resetPage"
          >
            <path
              d="M804.248575 157.915589l0 431.250908L393.361461 589.166497 393.361461 407.414013l-265.620613 227.143277 265.620613 227.01741L393.361461 679.95013l456.278931 0c25.086351 0 45.391816-20.36277 45.391816-45.39284L895.032208 157.915589 804.248575 157.915589 804.248575 157.915589zM804.248575 157.915589"
            ></path>
          </svg>
          <svg width="20px" height="20px" class="serachIco" viewBox="0 0 20 20">
            <path
              d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"
            ></path>
          </svg>
        </div>
        <button
          :class="
            'searchItem searchInput ' +
            theme +
            'btn' +
            ' btn-reset' +
            (ext.iconBadgeAnim ? ' anim' : '')
          "
          @click="clear"
          :title="i18n.closeAllBtn"
          @mouseenter="resetPage"
        ></button>
        <!-- <button :class="'searchItem searchInput btn btn-change'" @click="changeView" :title="i18n.changeViewBtn" @mouseenter="resetPage"></button> -->
        <div
          id="more"
          class="searchItem"
          @mouseenter="showMore"
          @mouseleave="hideMore"
          v-show="menuMoreShow"
        >
          <button :class="['searchInput', theme + 'btn', 'btn-more']"></button>
          <ul id="group-list" v-show="moreShow">
            <li @click="changeTheme" :class="[theme + 'group-li', theme + 'huanfu']"></li>
            <li @click="changeView" :class="[theme + 'group-li', showViewMod + 'viewmod']"></li>
            <li @click="setMore" :class="[theme + 'group-li', 'setmore']"></li>
          </ul>
        </div>

        <!-- todo -->
        <div id="group" class="searchItem" @mouseenter="showGroup" @mouseleave="hideGroup">
          <button :class="['searchInput', theme + 'btn', 'btn-group']">
            {{ group.list[groupIndex].name }}
            <!-- {{ group.list[0] }} -->
          </button>
          <ul id="group-list" v-show="groupShow">
            <li
              :class="theme + 'group-li'"
              v-for="(item, index) in group.list"
              v-if="index !== groupIndex"
              @click="changeGroup(index)"
              :title="item.name"
              :key="index"
            >
              {{ item.name }}
            </li>
            <li @click="setGroup" :class="[theme + 'group-li', 'setting']"></li>
          </ul>
        </div>

        <div id="emptyResult" v-show="searcher.empty">
          <p class="title">{{ i18n.searcherEmpty }}</p>
          <p class="search-webstore">
            <!-- edge -->
            <button class="btn" @click="goChromeStoreSearch">{{ i18n.tipsUrl }}</button>
            <!-- <button class="btn" @click="goChromeStoreSearch">{{i18n.tipsEdgeUrl}}</button> -->
          </p>
        </div>
      </div>

      <div id="123" v-if="showViewMod == 'row'">
        <ext-item
          :data-list="getEnbledExtList"
          :data-id="theme + 'showList'"
          data-locked="locked"
          :searching="searcher.doing"
          :hover="hover"
          :isShowName="isShowName"
        >
          <template slot="empty">
            <li class="empty" v-if="getEnbledExtList.length === 0">{{ i18n.emptyShowListCon }}</li>
          </template>
        </ext-item>
        <ext-item
          :data-list="getDisabledExtList"
          data-id="hideList"
          :searching="searcher.doing"
          :hover="hover"
        ></ext-item>
      </div>

      <div id="456" v-else>
        <ext-item2
          :data-list="getEnbledExtList"
          :data-id="theme + 'showList'"
          data-locked="locked"
          :searching="searcher.doing"
          :hover="hover"
        >
          <template slot="empty">
            <li class="empty" v-if="getEnbledExtList.length === 0">{{ i18n.emptyShowListCon }}</li>
          </template>
        </ext-item2>
        <ext-item2
          :data-list="getDisabledExtList"
          data-id="hideList"
          :searching="searcher.doing"
          :hover="hover"
        ></ext-item2>
      </div>
    </div>

    <!--  -->
    <div id="allEmptyTips" v-if="ext.allEmpty">
      <span class="title">{{ i18n.tipsTitle }}</span>
      <span class="desc">
        <span class="con">{{ i18n.tipsCon }}</span>
        <!-- edge -->
        <a :href="chromeStore" target="_blank" @click="goChromeStore">{{ i18n.tipsUrl }}</a>
        <!-- <a :href="chromeStore" target="_blank" @click="goChromeStore">{{i18n.tipsEdgeUrl}}</a> -->
      </span>
    </div>

    <div
      id="rightMenu"
      :class="[rightMenu.showClass]"
      :style="{ left: rightMenu.left, right: rightMenu.right, top: rightMenu.top }"
      @mouseenter="rightMemuEnter"
      @mouseleave="rightMemuLeave"
    >
      <div class="name" :style="{ background: rightMenu.backgroundColor }">
        {{ rightMenu.name }}
      </div>
      <ul>
        <li
          v-for="(item, index) in rightMenu.content"
          @click="item.handle"
          :disabled="item.disabled"
          :key="index"
          :style="{ background: rightMenu.backgroundColor }"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>

    <div
      id="flatMenu"
      :class="[flatMenu.showClass]"
      :style="{ left: flatMenu.left, right: flatMenu.right, top: flatMenu.top }"
      @mouseenter="flatMemuEnter"
      @mouseleave="flatMemuLeave"
    >
      <ul>
        <li
          v-for="(item, index) in flatMenu.content"
          @click="item.handle"
          :disabled="item.disabled"
          :key="index"
          :style="{ background: flatMenu.backgroundColor }"
        >
          <!-- {{item.name}} -->
          <div :style="{ backgroundImage: `url(${item.backgroundUrl})` }"></div>
        </li>
      </ul>
    </div>

    <canvas id="getColorByCanvas" style="display: none"></canvas>
  </div>
</template>

<script>
import i18n from '@/lib/i18n'
import ExtItem from '@/components/ExtItem'
import ExtItem2 from '@/components/ExtItem2'
import Dialog from '@/components/Dialog'
import * as Common from '@/lib/common'
import * as Extension from '@/lib/extension'
import * as Storage from '@/lib/storage'
import * as Util from '@/lib/util'
import * as Rank from '@/lib/rank'

export default {
  data() {
    return {
      // 
      i18n: i18n(),
      language: chrome.i18n.getUILanguage(),
      ext: {
        extList: [],
        iconBadgeAnim: false,
        allEmpty: false,
      },
      rightMenu: {
        name: '',
        showClass: '',
        left: 0,
        top: 0,
        backgroundColor: '#000',
        content: [],
        hover: false,
      },
      flatMenu: {
        name: '',
        showClass: '',
        left: 0,
        top: 0,
        backgroundColor: '#000',
        content: [],
        hover: false,
      },
      searcher: {
        doing: false,
        text: '',
        empty: false,
      },
      hover: {
        doing: false,
        listName: '',
      },
      group: {
        list: [
          {
            name: '',
            lock: {},
          },
        ],
      },
      fix: {},
      ruleable: {},
      groupIndex: 0,
      groupShow: false,
      moreShow: false,
      menuMoreShow: false,
      showIconSize: Common.ShowIconSize,
      showWindowSize: Common.WindowSizeDefaultColum,
      showViewMod: 'row',
      theme: '',
      orderHandle: function () {},
      isSupported: false,
      isResetTip: false,
      showDialog: false,
      dialogTitle: '',
      dialogContent: '',
      confirmText: '',
      dangerText: '',
      cancelText: '',
      dialogType: '',
      isShowName: false,
      isHoverMenu: true,
    }
  },
  watch: {
    groupIndex: (val, oldVal) => {
      localStorage.setItem('_groupIndex_', val)
    },
    'ext.extList': {
      handler: (_new, _old) => {
        setTimeout(() => {
          Extension.addIconBadge()
        }, 0)
      },
      deep: true,
    },
  },
  components: {
    ExtItem,
    ExtItem2,
    Dialog,
  },
  computed: {
    chromeStore() {
      // edge
      return `https://chrome.google.com/webstore?hl=${this.language}`
      // return `https://microsoftedge.microsoft.com/addons?hl=${this.language}`
    },
    getEnbledExtList() {
      let list = this.ext.extList.filter((item) => {
        if (this.searcher.doing) {
          if (item.enabled && item['isSearched']) {
            if (this.group.list[this.groupIndex].lock[item.id]) {
              item.isLocked = true
            } else {
              item.isLocked = false
            }
            return true
          }
        } else {
          if (item.enabled) {
            if (this.group.list[this.groupIndex].lock[item.id]) {
              item.isLocked = true
            } else {
              item.isLocked = false
            }
            return true
          }
        }
      })
      return list.sort(this.orderHandle)
    },
    getDisabledExtList() {
      let list = this.ext.extList.filter((item) => {
        if (this.searcher.doing) {
          if (!item.enabled && item['isSearched']) {
            if (this.group.list[this.groupIndex].lock[item.id]) {
              item.isLocked = true
            } else {
              item.isLocked = false
            }
            return true
          }
        } else {
          if (!item.enabled) {
            if (this.group.list[this.groupIndex].lock[item.id]) {
              item.isLocked = true
            } else {
              item.isLocked = false
            }
            return true
          }
        }
      })
      return list.sort(this.orderHandle)
    },
    getShowWindowSize() {
      return Common.WindowSizeByColum[this.showWindowSize || Common.WindowSizeDefaultColum]
    },
    getShowIconSize() {
      return this.showIconSize
    },
  },
  methods: {
    focus(e) {
      Util.resetHandle()
      e.target.focus()
    },
    // 
    extClick(item) {
      Rank.set(item.id)
      Util.onoff(item)
    },
    clear() {
      if (this.isResetTip) {
        this.showDialog = true
        this.dialogType = 'danger'
        this.dialogTitle = this.i18n.resetTipTitle
        this.dialogContent = this.i18n.resetTipContent
        this.dangerText = this.i18n.resetTipDangerText
        this.cancelText = this.i18n.resetTipCancelText
      } else {
        Util.clear()
      }
    },
    clickCancel() {
      // 
      Storage.set('_isResetTip_', 1)
      this.isResetTip = false
      Util.setGroup()
    },
    clickDanger() {
      // 
      Storage.set('_isResetTip_', 1)
      this.isResetTip = false
      // 
      Util.clear()
    },
    changeTheme() {
      if (this.theme && this.theme === 'dark') {
        this.theme = ''
        Storage.set('_showModTheme_', 'manual')
        Storage.set('_showViewTheme_', '')
      } else {
        this.theme = 'dark'
        Storage.set('_showModTheme_', 'manual')
        Storage.set('_showViewTheme_', 'dark')
      }
      Util.hideMore()
    },
    changeView() {
      if (this.showViewMod === 'row') {
        this.showViewMod = 'column'
        Storage.set('_showViewMod_', 'column')
        this.showIconSize = 0
        this.showWindowSize = 1
      } else {
        this.showViewMod = 'row'
        Storage.set('_showViewMod_', 'row')
        let showIconSize = Storage.get('_showIconSize_')
        if (showIconSize) {
          this.showIconSize = showIconSize
        }
        let showWindowSize = Storage.get('_showColumn_')
        if (showWindowSize) {
          this.showWindowSize = showWindowSize
        }
      }
      Util.track('GA_EVENT', {
        category: 'switch',
        action: this.showViewMod,
        label: this.showViewMod,
      })
    },
    setMore() {
      Util.setMore()
    },
    extEnter(item) {
      if (this.showViewMod === 'row') {
        Util.enter(item, this.isHoverMenu)
      } else {
        Util.enter2(item)
      }
    },
    extOption(item) {
      Util.option(item)
    },
    extLeave(item) {
      if (this.showViewMod === 'row') {
        Util.leave(item)
      } else {
        Util.leave2(item)
      }
    },
    search() {
      Util.search()
    },
    searchByChromeStore(e) {
      if (e.keyCode === 13) {
        this.goChromeStoreSearch(e)
      }
    },
    cancelSearch() {
      Util.cancelSearch()
    },
    enterSearch() {
      this.goChromeStoreSearch()
    },
    showGroup() {
      Util.resetHandle()
      Util.showGroup()
    },
    hideGroup() {
      Util.hideGroup()
    },
    showMore() {
      Util.resetHandle()
      Util.showMore()
    },
    hideMore() {
      Util.hideMore()
    },
    changeGroup(index) {
      Util.changeGroup(index)
    },
    setGroup() {
      Util.setGroup()
    },
    goChromeStore(e) {
      chrome.tabs.create({
        url: e.target.href,
      })
    },
    rightMemuLeave() {
      Util.resetHandle()
    },
    rightMemuEnter() {
      this.rightMenu.hover = true
    },
    flatMemuEnter() {
      this.flatMenu.hover = true
    },
    flatMemuLeave() {
      Util.resetHandle()
    },
    resetPage() {
      Util.resetHandle()
    },
    goChromeStoreSearch(e) {
      e && e.preventDefault()
      if (this.searcher.text.trim()) {
        chrome.tabs.create({
          // edge
          url: `https://chrome.google.com/webstore/search/${encodeURIComponent(
            this.searcher.text
          )}?hl=${this.language}`,
          // 'url': `https://microsoftedge.microsoft.com/addons/search/${encodeURIComponent(this.searcher.text)}?hl=${this.language}`
        })
      }
    },
    goSupport(e) {
      e.preventDefault()
      Storage.set('_isSupported_', 1)
      chrome.tabs.create({
        url: `/option.html#support`,
      })
    },
  },
  async beforeCreate() {
    window._vm = this

    let _storage = await Storage.getAll(this)
    let _allExt = await Extension.getAll(this, true)

    // 
    // let showViewMod = localStorage.getItem('_showViewMod_') || 'row'
    let showViewMod = Storage.get('_showViewMod_') || 'row'
    this.showViewMod = showViewMod

    // 
    // let themeMod = localStorage.getItem('_showModTheme_') || 'manual'
    let themeMod = Storage.get('_showModTheme_') || 'manual'

    // 
    if (themeMod === 'manual') {
      if (Storage.get('_showViewTheme_') === 'dark') {
        this.theme = 'dark'
      } else {
        this.theme = ''
      }
    } else {
      this.theme = Util.detectColorTheme()
    }

    // 
    if (this.showViewMod === 'row') {
      let showWindowSize = Storage.get('_showColumn_')
      if (showWindowSize) {
        this.showWindowSize = showWindowSize
      } else {
        Storage.set('_showColumn_', Common.WindowSizeDefaultColum)
      }
      let showIconSize = Storage.get('_showIconSize_')
      if (showIconSize) {
        this.showIconSize = showIconSize
      } else {
        Storage.set('_showIconSize_', Common.ShowIconSize)
      }
    } else {
      this.showWindowSize = 1
      this.showIconSize = 0
    }

    let isSupported = Storage.get('_isSupported_')
    if (!isSupported) {
      this.isSupported = true
    }

    let isResetTip = Storage.get('_isResetTip_')
    if (!isResetTip) {
      this.isResetTip = true
    }

    //
    if (Storage.get('_switch_show_name_') === 'open') {
      this.isShowName = true
    } else {
      this.isShowName = false
    }

    //
    if (Storage.get('_radio_ext_menu_') === 'click') {
      this.isHoverMenu = false
    } else {
      this.isHoverMenu = true
    }

    // 
    Storage.initGroup(this)

    this.ruleable = (await Storage.getLocalItem('_ruleable_')) || {}

    // 
    this.orderHandle = Extension.orderHandle(_storage)

    if (_allExt && _allExt.length === 0) {
      this.ext.allEmpty = true
    } else {
      this.ext.extList = _allExt
      // itemfix
      this.ext.extList.map((item) => {
        if (this.fix[item.id]) {
          item.isFixed = true
          Extension.on(item)
        } else {
          item.isFixed = false
        }

        if (this.ruleable[item.id] && this.ruleable[item.id].enabled) {
          item.isRuled = true
        } else {
          item.isRuled = false
        }
        return item
      })
    }

    // 
    if (Storage.get('_switch_show_fast_') !== 'close') {
      this.menuMoreShow = true
    }

    // 
    Util.init(this)

    // 
    // setTimeout(() => {
    //   if(localStorage.getItem('_func_show_update_tip') != '9.3.9') {
    //     localStorage.setItem('_func_show_update_tip', '9.3.9')
    //     Object.keys(localStorage).forEach(item => item.indexOf('_imageColor_') != -1 ? localStorage.removeItem(item) : '')
    //     this.showDialog = true
    //     this.dialogType = 'confirm'
    //     this.dialogTitle = this.i18n.updateTipTitle + " Version:9.3.9"

    //     if(this.language === 'zh-CN' || this.language === 'zh-TW') {
    //       this.dialogContent = ''
    //     } else {
    //       this.dialogContent = 'The rules can be copied!'
    //     }
    //     this.confirmText = this.i18n.tipConfirmText
    //   }
    // }, 300);
  },
  directives: {
    focus: {
      // 
      inserted: function (el) {
        el.focus()
      },
    },
  },
}
</script>

<style>
* {
  padding: 0;
  margin: 0;
}

body {
  user-select: none;
  -webkit-user-select: none;
  position: relative;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: #d8d8d8;
}

.gclearfix {
  zoom: 1;
}

.gclearfix::after {
  clear: both;
  content: '';
  display: block;
  height: 0;
  visibility: hidden;
}

#popup {
  position: relative;
  min-width: 496px;
  min-height: 300px;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

#darkpopup {
  position: relative;
  min-width: 496px;
  min-height: 300px;
  background-color: #121212;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

#wrap {
  position: relative;
  min-height: 125px;
  padding: 20px;
  box-sizing: border-box;
  background-color: #fff;
  z-index: 2;
}
#darkwrap {
  position: relative;
  min-height: 125px;
  padding: 20px;
  box-sizing: border-box;
  background-color: #121212;
  z-index: 2;
}
#search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 46px;
  margin-bottom: 20px;
  box-sizing: border-box;
}
#search .searchInput {
  height: 100%;
  box-sizing: border-box;
  border: none;
  outline: none;
  border-radius: 4px;
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
}
#search .searchItem {
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  position: relative;
  margin-left: 4px;
}
#search #searchBox {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  height: 100%;
  position: relative;
}
#search .searcher {
  width: 100%;
  line-height: 1em;
  padding: 0 40px 0 45px;
  font-size: 16px;
  background: #f5f5f5;
  color: #212121;
}
#search .searchBtn {
  display: none;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  fill: #717171;
  cursor: pointer;
  border: 1px solid #858585;
  border-radius: 4px;
  transition: 0.2s ease-in-out;
}
#search .searchEnter {
  right: 42px;
}
#search .searchEmpty {
  right: 10px;
}
#search .searchBtn:hover {
  background-color: #6494f4;
  border-color: #6494f4;
  fill: #fff;
}
#search .serachIco {
  fill: #717171;
  position: absolute;
  left: 15px;
  top: 50%;
  -webkit-transform: translateY(-58%);
  transform: translateY(-58%);
}
#search .btn {
  height: 100%;
  font-size: 16px;
  cursor: pointer;
  color: #717171;
  text-align: center;
  background-color: #f5f5f5;
  padding: 0 12px;
  outline: none;
}
#search .darkbtn {
  height: 100%;
  font-size: 16px;
  cursor: pointer;
  color: #717171;
  text-align: center;
  background-color: #313131;
  padding: 0 12px;
  outline: none;
}
#search .darkbtn:hover {
  color: #fff;
  background-color: #474747;
  border-color: #474747;
}
#search .btn:hover {
  color: #fff;
  background-color: #6494f4;
  border-color: #64acf4;
}
#search .btn:active {
  transition: none;
  color: #fff;
  background-color: #5c5e6f;
  border-color: #353640;
}
