<template>
  <ul
    :id="dataId"
    :class="{ gclearfix: true, 'ext-list2': !isDark, 'dark-ext-list2': isDark }"
    :locked="dataLocked"
  >
    <li
      v-for="item in dataList"
      :key="item.id"
      :data-id="item.id"
      :class="{ hover: item.isHover }"
      :style="getStyle(item)"
      :locked="item.isLocked"
      :fixed="item.isFixed"
      :ruled="item.isRuled"
      :searched="item.isSearched"
      :mark="item.showMark"
      @mousedown.left.prevent="extClick(item)"
      @mouseenter="extEnter(item)"
      @mouseleave="extLeave(item)"
    >
      <i v-if="item.showType" :style="'background-color: ' + item.showColor[item.showMark]">{{
        item.showType
      }}</i>
      <div :name-data-id="item.id" :style="getStyleName(item)">{{ item.name }}</div>
    </li>
    <slot name="empty"></slot>
  </ul>
</template>

<script>
export default {
  name: 'ExtenItem2',
  props: {
    dataList: Array,
    dataId: String,
    dataLocked: String,
    searching: Boolean,
    hover: Object,
    from: String,
  },
  computed: {
    isDark: function () {
      return this.$parent.theme === 'dark'
    },
    getStyle: function () {
      return function (item) {
        // console.log('extitem2')
        if (item['showBase64'] && item['showBase64'][item['showMark']]) {
          if (this.from === 'option') {
            item['showMark'] = item.isLocked ? 'original' : 'filter'
          } else {
            if (this.searching) {
              // item['showMark'] = item.isSearched ? 'original' : 'dinginess'
            } else {
              if (this.hover && this.hover.doing && this.dataId === this.hover.listName) {
                if (item.enabled) {
                  item['showMark'] = 'original'
                } else {
                  // item['showMark'] = item.isHover ? 'original' : 'dinginess'
                  item['showMark'] = item.isHover ? 'original' : 'filter'
                }
              } else {
                item['showMark'] = item.enabled ? 'original' : 'filter'
              }
            }
          }
          // if(item.id === 'dogkpdfcklifaemcdfbildhcofnopogp'){
          //   console.log(`1. background-image:url('${item['showBase64'][item['showMark']]}');`)
          // }
          return `background-image:url('${
            item['showBase64'][item['showMark'] + this.$parent.theme]
          }');`
          // return `background-image:url('${item['showBase64'][item['showMark']]}');`
        } else {
          if (item.enabled) {
            return `background-image:url('${item['showIcon']}');`
          } else {
            // if(item.id === 'dogkpdfcklifaemcdfbildhcofnopogp'){
            //   console.log(`2. opacity:.5; filter:grayscale(1);background-image:url('${item['showIcon']}');`)
            // }
            // return `background-color: #f9f9f9;`
            if (this.$parent.theme === 'dark') {
              return `background-color: #1c1c1c;`
            } else {
              return `background-color: #f9f9f9;`
            }
            // return `background-image:url('${item['showIcon']}'); opacity:.5; filter:grayscale(1);`
            // return "background-image: url('data:image/svg+xml;base64,');"
          }
        }
      }
    },
    getStyleName: function () {
      return function (item) {
        if (this.$parent.theme === 'dark') {
          return `color: #d9d9d9;`
        } else {
          if (item.isHover) {
            return `color:${item.showColor['original']}`
          } else {
            return `color: #292929;`
          }
        }
      }
    },
  },
  methods: {
    extClick(item) {
      this.$parent.extClick && this.$parent.extClick(item)
    },
    extEnter(item) {
      this.$parent.extEnter && this.$parent.extEnter(item)
      return
    },
    extLeave(item) {
      this.$parent.extLeave && this.$parent.extLeave(item)
    },
  },
}
</script>

