<template>
  <ul
    :id="dataId"
    :class="{ gclearfix: true, 'ext-list': !isDark, 'dark-ext-list': isDark }"
    :locked="dataLocked"
  >
    <li
      v-for="item in dataList"
      :key="item.id"
      :data-id="item.id"
      :class="{ hover: item.isHover }"
      :style="getStyle(item)"
      :locked="item.isLocked"
      :fixed="item.isFixed"
      :ruled="item.isRuled"
      :searched="item.isSearched"
      :mark="item.showMark"
      @mousedown.left.prevent="extClick($event, item)"
      @mouseenter="extEnter(item)"
      @mouseleave="extLeave(item)"
    >
      <i v-if="item.showType" :style="'background-color: ' + item.showColor[item.showMark]">{{
        item.showType
      }}</i>
      <!-- <span class="options" @mousedown.left.prevent="extClick($event, item)"></span> -->
      <span
        v-if="item.isHover && isShowClick"
        class="options"
        @mousedown.left.prevent="extClick($event, item)"
      ></span>

      <span v-if="isShowName" class="nickFront">{{ item.nickName }}</span>
    </li>
    <slot name="empty"></slot>
  </ul>
</template>

<script>
export default {
  name: 'ExtItem',
  props: {
    dataList: Array,
    dataId: String,
    dataLocked: String,
    searching: Boolean,
    hover: Object,
    from: String,
  },
  computed: {
    isDark: function () {
      return this.$parent.theme === 'dark'
    },
    isShowName: function () {
      return this.$parent.isShowName
    },
    isShowClick: function () {
      return !this.$parent.isHoverMenu
    },
    getStyle: function () {
      return function (item) {
        // console.log('extitem1')
        if (item['showBase64'] && item['showBase64'][item['showMark']]) {
          if (this.from === 'option') {
            item['showMark'] = item.isLocked ? 'original' : 'filter'
          } else {
            if (this.searching) {
              // item['showMark'] = item.isSearched ? 'original' : 'dinginess'
            } else {
              if (this.hover && this.hover.doing && this.dataId === this.hover.listName) {
                if (this.$parent.theme === 'dark') {
                  item['showMark'] = item.isHover ? 'original' : 'filter'
                } else {
                  // item['showMark'] = item.isHover ? 'original' : 'dinginess'
                  // dinginess
                  // item['showMark'] = item.isHover ? 'original' : 'filter'
                  item['showMark'] = item.isHover
                    ? 'original'
                    : item.enabled
                    ? 'original'
                    : 'filter'
                }
              } else {
                item['showMark'] = item.enabled ? 'original' : 'filter'
              }
            }
          }
          // console.error(item['showMark'])
          // if(item.id === 'gbmdgpbipfallnflgajpaliibnhdgobh'){
          //     return `background-image:url('${item['showBase64']['originalDark']}');`
          // }
          // item['showMark'] = item['showMark'] + this.$parent.theme
          // console.error(item['showMark'])
          return `background-image:url('${
            item['showBase64'][item['showMark'] + this.$parent.theme]
          }');`
        } else {
          if (item.enabled) {
            return `background-image:url('${item['showIcon']}');`
          } else {
            // if(item.id === 'dogkpdfcklifaemcdfbildhcofnopogp'){
            //   console.log(`2. opacity:.5; filter:grayscale(1);background-image:url('${item['showIcon']}');`)
            // }
            if (this.$parent.theme === 'dark') {
              return `background-color: #1c1c1c;`
            } else {
              return `background-color: #f9f9f9;`
            }
            // return `background-image:url('${item['showIcon']}'); opacity:.5; filter:grayscale(1);`
            // return "background-image: url('data:image/svg+xml;base64,');"
          }
        }
      }
    },
    getStyleName: function () {
      return function (item) {
        return `color:${item.showColor['original']}`

        // return item.enabled ? `color:${item.showColor['original']}` : `color:${item.showColor['filter']}`
      }
    },
  },
  methods: {
    extClick(e, item) {
      if (e.target && e.target.className == 'options') {
        this.$parent.extOption && this.$parent.extOption(item)
      } else {
        this.$parent.extClick && this.$parent.extClick(item)
      }
    },
    extEnter(item) {
      this.$parent.extEnter && this.$parent.extEnter(item)
    },
    extLeave(item) {
      this.$parent.extLeave && this.$parent.extLeave(item)
    },
  },
}
</script>

